.footer-nav {
  margin-top: 65px;
  margin-bottom: 32px;
  counter-reset: list;
}
.footer-nav__item {
  position: relative;
  display: inline-block;
  min-width: 213px;
  margin-right: 4.5%;
  margin-bottom: 10px;
  transition: all 0.3s;
}
.footer-nav__item:before {
  display: inline-block;
  position: absolute;
  right: 0;
  top: 0;
  content: "0" counter(list);
  counter-increment: list;
  opacity: 0.2;
  font-weight: 500;
  font-size: 24px;
}
.footer-nav__item:hover:after {
  width: 100%;
  background-color: #E43828;
}
.footer-nav__item:hover .footer-nav__link {
  color: #FFFFFF;
  text-decoration: none;
}
.footer-nav__link {
  color: #FFFFFF;
  display: block;
  padding-right: 50px;
  min-width: auto;
}
.footer-nav__link:hover {
  color: #FFFFFF;
  text-decoration: none;
}
.footer-nav__link:hover:after {
  width: 100%;
  background-color: #E43828;
  opacity: 1;
}
.footer-nav__link:hover:before {
  opacity: 1;
}
